<template>
  <div class='about marTop_50'>
    <router-view></router-view> 
    <div class='container'>
      <div class='row'>
        <div class='col-xs-12'>
          <div class="panel panel-default">
              <div class="panel-body">
                  <div id="wrapper">
                      <!-- 品牌列表开始 -->
                      <div class="brand-list">
                          <div class="brand-bd cle" id="brand-waterfall">
                              <!-- 循环字母模块 item -->
                              <div @click='img_click(item)' class="item cursor" v-for='(item,index) in pics'>
                                  <p> <img class='cover' v-lazy='item.img' style="width:100%;"></p>
                                  <!-- <p> <img src='../assets/img/1259.jpg' style="width:100%;"></p> -->
                                  <p class="margin-clear">
                                    <label class="text-color-bg1" v-text='item.title'></label>
                                  </p>
                                  <p class="margin-clear">
                                    <label class="text-color-bg1">说明:</label>
                                    <label v-text='item.content'></label>
                                  </p>
                                  <p class="margin-clear">
                                    <label class="text-color-bg1">日期:</label>
                                    <label v-text='item.date'></label>
                                  </p>
                              </div>
                          </div>
                      </div>
                      <!-- 品牌列表 end -->
                  </div>
              </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import da from '@/assets/js/data.js'
require('../assets/js/cascade.js')

export default {
  data () {
      return {
        msg: 'pic Vue',
        name: 'Reborn',
        count:0,
        pics:[],
        show:true,
      }
    },
  components: {

  },
  created(){
    this.loadInfo()
  },
  mounted() {;
    setTimeout(function(){
      $('#brand-waterfall').cascade();
    },350)
  },
  methods:{
    foo() {
      console.log(jQuery.fn.jquery)
    },
    img_click(item){
      // console.log(item.img);
      this.$router.push({
        path:'/pic:'+item.id
      });
    },
    loadInfo(){
      this.pics = [];
      var data = da.pic;
      if (data) {
        for (var i = 0; i < data.length; i++) {
          this.pics.push(data[i]);
        };
      }else{
        console.warn('data is null')
      };
    },
  }
}
</script>

<style scoped>
.clear {clear:both; height:0; font-size:0; line-height:0; overflow:hidden;}
.cle:after, .clearfix:after, .clear_f:after, .cle_float:after{content:".";display:block;height:0;clear:both;visibility:hidden}
.cle, .clearfix, .clear_f, .cle_float{zoom:1}
.fl {float:left;}
.fr {float:right;}

a{text-decoration:none; color:#333; -webkit-transition: color .2s;-moz-transition: color .2s;-o-transition: color .2s; -ms-transition: color .2s;transition: color .2s;}
a:hover{text-decoration:underline; color: #ff7d8e;}
a:focus{outline:none;}
::selection {background:#ff7d8e; color:#fff;} 


#wrapper{min-width: 250px; max-width: 1008px; margin: 15px auto;}
/*品牌列表*/
.brand-list{ margin-left: -10px;}
.brand-list .brand-bd{position: relative; margin-left: 10px; margin-top: 5px;}
.brand-list .item,.brand-list .additem{position: absolute; width: 202px; background-color: #f8f8f8; border: 1px solid #f8f8f8; padding: 18px; top:0; left: 40%; -webkit-transition: all 0.3s;-moz-transition: all 0.3s;-o-transition: all 0.3s;-ms-transition: all 0.3s;transition: all 0.3s;}
.brand-list .hover{box-shadow: 0px 1px 8px rgba(200,200,200,.6); background-color: #fff; border-color: #c8c8c8;}
.brand-list .additem{padding: 0; width: 240px; border: none; }

.brand-list .item h3{font-size: 42px; color: #666; font-family: arial; font-weight: bold; line-height: .8; margin-bottom: 20px;}
.brand-list .item p{margin-bottom: 15px;}
</style>



