<template>
  <div class='pic_b'>
    <div class='container' style='margin-top:10px;'>
      <div class='row'>
        <div class='col-xs-12 col-sm-6'>
          <!-- <img src="../assets/img/1.jpg" class="img-thumbnail"> -->
          <img id='img_b' v-lazy='imgs.img' class="img-responsive big_img img-thumbnail">
        </div>
        <div class='col-xs-12 col-sm-6 posFixd'>
          <div class='col-xs-9 col-sm-9'>
            <div class='comment_box marTop_50'>
              <h4 class="text-left" v-text='imgs.title'></h4>
              <p class="text-left" v-text='imgs.content'></p>
              <p class="text-left" v-text='imgs.date'></p>
            </div>
          </div>
          <div class='col-xs-3 col-sm-3'>
            <div @click='back' class='closed cursor'>
              <i class='fa fa-close fa-lg'></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import u from '@/assets/js/utils.js'
import da from '@/assets/js/data.js'

export default {
  data () {
      return {
        msg: 'pic_big Vue',
        name: 'Reborn',
        imgs:'../assets/img/default.png',
      }
    },
  components: {

  },
  created(){
    var img = u.GetUrl(6);
    // console.log(img)
    this.imgs = this.getImg(img);
    // console.log(this.imgs)
  },
  mounted() {
    var _this = this;
    document.getElementById('img_b').onload=function(){
            // 加载完成 
            _this.mid();
    };
    $(window).resize(function() {
            _this.mid();
    });
  },
  methods:{
    foo() {
      console.log('foo')
    },
    mid(){
      let wh = $(window).height();
      let imgh = $('.big_img').height();
      // console.log(imgh);
      $('.big_img').css('top',-imgh/2);
      $('.pic_b').height(wh);
    },
    getImg(obj){
      var res;
      var json = da.pic;
      for (var i = 0; i < json.length; i++) {
        if (json[i].id == obj) {
          res = json[i];
          return res
        };
      };
    },
    back(){
      history.back()
    }
  }
}
</script>

<style scoped>
.pic_b{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 9999999;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.75); 
  /* display: none; */
}
.closed{
    width: 45px;
    height: 45px;
    color: #fff;
    border-radius: 50%;
    border: 5px solid #fff;
    line-height: 35px;
    margin: auto;
    /*position: absolute;
    left: 0;
    right: 0;
    bottom: 35px;*/
}
.big_img{
  width: 75%;
  /*max-height:450px;*/
  position: relative;
  margin-top: 70%;
}
.comment_box{
  color: #fff;
}
@media screen and (max-width: 420px){
  .posFixd{
    position: fixed;
    bottom: 0;
  }
  .big_img{
    /*width: 80%;*/
    /*max-height:450px;*/
    position: relative;
    margin-top: 75%;
  }
}
@media screen and (min-width: 1200px ){
  .big_img{
    width: 60%;
    position: relative;
    margin-top: 55%;
  }
}
</style>



